.share-popup {
  position: fixed;
  width: 100%;
  height: 90%;
  top: 10%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.45);
  z-index: 6;
  display: flex;
  justify-content: center;
  align-items: center;
}
.share-popup .popup-content {
  width: 50%;
  height: 50%;
  background-color: #404649;
  border: 2px solid #99CBF9;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 2%;
}
.share-popup .popup-top {
  width: 90%;
  height: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.share-popup .popup-top .popup-title {
  color: #ffffff;
  font-size: 18px;
  font-weight: 500;
}
.share-popup .popup-top .popup-close {
  font-size: 30px;
  font-weight: 600;
  color: #cecece;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
}
.share-popup .share-methods {
  width: 90%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.share-popup .share-methods .popup-links {
  width: 100%;
  height: 30%;
  margin-bottom: 2%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 1%;
}
.share-popup .share-methods .popup-links .popup-link {
  width: 3rem;
  height: 3rem;
  border: 2px solid #51595C;
  border-radius: 0.5rem;
  color: #99CBF9;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  user-select: none;
}
.share-popup .share-methods .popup-links .popup-link:hover {
  border-color: #99CBF9;
}
.share-popup .popup-clipbar {
  width: 90%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #343637;
  border-radius: 0.5rem;
}
.share-popup .popup-clipbar .popup-link-bar {
  width: 83%;
  height: 90%;
  overflow: hidden;
  background-color: #343637;
  color: #cecece;
  font-size: 16px;
  font-weight: 500;
  border: 0;
}
.share-popup .popup-clipbar .popup-link-bar:active {
  border: 0;
}
.share-popup .popup-clipbar .popup-link-bar:focus {
  border: 0;
  outline: 0;
  caret-color: #343637;
}
.share-popup .popup-clipbar .popup-copy-button {
  width: 15%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #404649;
  font-weight: 600;
  border-radius: 0.6rem;
  background-color: #99CBF9;
  cursor: pointer;
  text-decoration: none;
  user-select: none;
}
.share-popup .popup-clipbar .popup-copy-button:hover {
  font-weight: 700;
  color: #343637;
}
