.homepage {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  padding-top: 3%;
}
.homepage .content {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: row;
  background-color: #404649;
  justify-content: space-between;
}
.homepage .content .featured-block {
  width: 22%;
  height: 80vh;
  min-height: 300px;
  border: 2px solid #5C5D5D;
  border-radius: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.homepage .content .featured-block .f-frame {
  width: 95%;
  height: 95%;
  margin-top: 0.1rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.homepage .content .featured-block .f-frame .f-title {
  width: 100%;
  height: 5%;
  margin-bottom: 3%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #cecece;
  font-weight: 600;
}
.homepage .content .featured-block .f-frame .f-author-block {
  width: 98%;
  height: 8%;
  margin-bottom: 2%;
  border-radius: 0.7rem;
  border: 2px solid #51595C;
  background-color: #51595C;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
  box-shadow: #394043 0 0 8px;
  transition: height 0.2s, border-color 0.25s ease-in;
}
.homepage .content .featured-block .f-frame .f-author-block:hover {
  height: 9%;
  border-color: #99CBF9;
  padding: 1%;
  width: 100%;
}
.homepage .content .featured-block .f-frame .f-author-block:hover .f-alert {
  font-size: 16px;
  font-weight: 600;
}
.homepage .content .featured-block .f-frame .f-author-block .f-avatar {
  width: 2.5rem;
  height: 2.5rem;
  background-color: #99CBF9;
  margin-right: 2%;
  border-radius: 0.5rem;
  box-shadow: #424242 0 0 5px;
}
.homepage .content .featured-block .f-frame .f-author-block .f-name {
  width: 58%;
  height: 40%;
  padding-left: 2%;
  text-align: left;
  font-size: 15px;
  color: #cecece;
  font-weight: 500;
  display: flex;
  align-items: center;
  overflow: hidden;
  margin-right: 2%;
}
.homepage .content .featured-block .f-frame .f-author-block .f-alert {
  width: 16%;
  height: 80%;
  background-color: #99CBF9;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  color: #404649;
}
.homepage .content .feed {
  width: 50%;
}
.homepage .content .feed .feed-banner {
  width: 97%;
  height: 3rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  padding-left: 3%;
  color: #cecece;
}
.homepage .content .feed .feed-footer {
  width: 100%;
  height: 5vh;
  margin-top: 5%;
  margin-bottom: 15%;
  color: #cecece;
  font-weight: 600;
  font-size: 16px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.homepage .content .balance {
  width: 22%;
  height: 80vh;
}
.homepage .content .balance-monitor {
  width: 100%;
  height: 4%;
  min-height: 300px;
  border: 2px solid #5C5D5D;
  border-radius: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 3%;
}
.homepage .content .balance-monitor .balance-frame {
  width: 95%;
  height: 95%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.homepage .content .balance-monitor .balance-frame .b-title {
  width: 100%;
  height: 10%;
  color: #cecece;
  font-size: 16px;
  font-weight: 600;
  padding-top: 2%;
  margin-bottom: 1%;
}
.homepage .content .balance-monitor .balance-frame .b-balance-block {
  width: 100%;
  height: 25%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.homepage .content .balance-monitor .balance-frame .b-balance-block .balance-value {
  width: auto;
  height: 50%;
  margin-right: 3%;
  color: #cecece;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
.homepage .content .balance-monitor .balance-frame .b-balance-block .currency {
  width: 2rem;
  height: 2rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
}
.homepage .content .balance-monitor .balance-frame .b-stats-block {
  width: 100%;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.homepage .content .balance-monitor .balance-frame .b-stats-block .stat-item {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.homepage .content .balance-monitor .balance-frame .b-stats-block .stat-item .stat-title {
  width: 100%;
  height: 49%;
  font-weight: 600;
  font-size: 16px;
  color: #cecece;
}
.homepage .content .balance-monitor .balance-frame .b-stats-block .stat-item .stat-value {
  width: 100%;
  height: 49%;
  font-weight: 500;
  font-size: 15px;
  color: #cecece;
}
.homepage .content .balance-story {
  width: 100%;
  height: 48%;
  min-height: 300px;
  border: 2px solid #5C5D5D;
  border-radius: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
