.post {
  width: 100%;
  height: auto;
  min-height: 20rem;
  margin-top: 3%;
  border-radius: 0.7rem;
  background-color: #51595C;
  box-shadow: #424242 0 0 10px;
}
.post .post-attribution {
  width: 100%;
  height: 5%;
  max-height: 5%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.post .post-attribution .author-credential {
  width: 70%;
  height: 90%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-left: 2%;
  padding-top: 2%;
  padding-bottom: 1.5%;
}
.post .post-attribution .author-credential .author-image {
  margin-right: 2%;
  max-width: 1.5rem;
  max-height: 1.5rem;
  border-radius: 0.3rem;
  box-shadow: #424242 0 0 5px;
}
.post .post-attribution .author-credential .author-name {
  width: 70%;
  height: 80%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-size: 16px;
  font-weight: 500;
  color: #cecece;
}
.post .post-attribution .post-date {
  width: 10%;
  height: 90%;
  margin-right: 2%;
  font-size: 14px;
  font-weight: 200;
  color: #cecece;
  border-radius: 0.3rem;
}
.post .post-image-block {
  background: #343637;
  background: linear-gradient(90deg, #343637 0%, #51595c 25%, #51595c 50%, #51595c 75%, #343637 100%);
}
.post .post-image-block .image-wrapper {
  width: 100%;
  position: relative;
  padding-top: 56.25%;
  overflow: hidden;
}
.post .post-image-block .post-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
.post .post-info-block {
  width: 100%;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.post .post-info-block .main-info-block {
  width: 95%;
  display: flex;
  margin-top: 3%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.post .post-info-block .main-info-block .post-title-link {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: flex-start;
  font-size: 20px;
  font-weight: 600;
  color: #cecece;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
}
.post .post-info-block .main-info-block .post-description {
  width: 100%;
  height: auto;
  margin-top: 3%;
  display: flex;
  justify-content: flex-start;
  text-align: left;
  font-size: 14px;
  font-weight: 200;
  color: #cecece;
  letter-spacing: 1px;
}
.post .post-info-block .main-info-block .open-post-button {
  width: 10%;
  height: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  color: #99CBF9;
  align-self: flex-start;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  transition: font-size 0.15s, color 0.15s ease-in;
}
.post .post-info-block .main-info-block .open-post-button:hover {
  color: #b4d9fc;
}
.post .post-info-block .post-controls {
  margin-top: 2%;
  margin-bottom: 2%;
  width: 95%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.post .post-info-block .post-controls .social-block {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.post .post-info-block .post-controls .social-block .likes-block {
  width: auto;
  height: 100%;
  min-width: 20%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.post .post-info-block .post-controls .social-block .likes-block .like-img {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
  user-select: none;
  transition: transform 0.1s ease-in;
}
.post .post-info-block .post-controls .social-block .likes-block .like-img:hover {
  transform: translateY(-5px);
}
.post .post-info-block .post-controls .social-block .likes-block .like-counter {
  margin-left: 10%;
  width: auto;
  height: 100%;
  color: #cecece;
  font-weight: 400;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.post .post-info-block .post-controls .social-block .comments-block {
  width: auto;
  height: 100%;
  min-width: 20%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.post .post-info-block .post-controls .social-block .comments-block .comm-img {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
  user-select: none;
  transition: transform 0.1s ease-in;
}
.post .post-info-block .post-controls .social-block .comments-block .comm-img:hover {
  transform: translateY(-5px);
}
.post .post-info-block .post-controls .social-block .comments-block .comm-counter {
  margin-left: 10%;
  width: auto;
  height: 100%;
  color: #cecece;
  font-weight: 400;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.post .post-info-block .post-controls .social-block .save-block {
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  cursor: pointer;
  user-select: none;
  transition: transform 0.1s ease-in;
}
.post .post-info-block .post-controls .social-block .save-block:hover {
  transform: translateY(-5px);
}
.post .post-info-block .post-controls .control-block {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.post .post-info-block .post-controls .control-block .share-button {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 5%;
  transition: transform 0.1s ease-in;
}
.post .post-info-block .post-controls .control-block .share-button:hover {
  transform: translateY(-5px);
}
.post .post-info-block .post-controls .control-block .report-button {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 5%;
  transition: transform 0.1s ease-in;
}
.post .post-info-block .post-controls .control-block .report-button:hover {
  transform: translateY(-5px);
}
.post .post-info-block .post-controls .control-block .options-button {
  width: 10%;
  height: 100%;
  font-size: 18px;
  font-weight: bold;
  color: #6e6e6e;
  letter-spacing: 2px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.post .post-info-block .post-controls .control-block .options-button:hover {
  color: #cecece;
  content: "";
}
