.user-identic {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background: #1a1a1a;
  background: -moz-linear-gradient(90deg, #1a1a1a 0%, #555555 18%, #555555 82%, #1a1a1a 100%);
  background: -webkit-linear-gradient(90deg, #1a1a1a 0%, #555555 18%, #555555 82%, #1a1a1a 100%);
  background: linear-gradient(90deg, #1a1a1a 0%, #555555 18%, #555555 82%, #1a1a1a 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#1a1a1a", endColorstr="#1a1a1a", GradientType=1);
}
.user-identic .identic-block {
  width: 75%;
  height: 100%;
  background-image: url("../../../../assets/icons/mock/mock-img6.jpg");
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 0.5rem;
  aspect-ratio: 16/9;
}
.user-identic .identic-block .id-user-image {
  margin-bottom: 0.5rem;
  margin-left: 1rem;
  width: 10rem;
  height: 10rem;
  background-color: rgba(228, 228, 228, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.7rem;
  aspect-ratio: 1/1;
}
.user-identic .identic-block .id-user-image .id-user-image-value {
  width: 90%;
  height: 90%;
  background-color: #99CBF9;
  border-radius: 0.7rem;
  box-shadow: #343637 0 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1/1;
}
.user-identic .identic-block .id-user-name {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.user-identic .identic-block .id-user-name .username-value {
  width: auto;
  height: 10%;
  background-color: rgba(228, 228, 228, 0.5);
  padding: 3%;
  border-radius: 0.5rem;
  justify-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
  text-shadow: #343637 0 0 10px;
}
.user-identic .identic-block .id-user-name .profile-share {
  margin-left: 0.5rem;
  min-width: 2rem;
  min-height: 2rem;
  border: 2px solid rgba(228, 228, 228, 0.5);
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: ease-in 0.1s ease-in;
}
.user-identic .identic-block .id-user-name .profile-share .share-img {
  width: 1.1rem;
  height: 1.1rem;
  transition: width 0.15s, height 0.15s ease-in;
}
.user-identic .identic-block .id-user-name .profile-share:hover {
  border-color: #ffffff;
}
.user-identic .identic-block .id-user-name .profile-share:hover .share-img {
  width: 1.4rem;
  height: 1.4rem;
}
.user-identic .identic-block .id-user-links {
  width: auto;
  height: 10%;
  max-width: 10rem;
  margin-right: 0.5rem;
  border-radius: 0.5rem;
  padding: 1.2% 0.5% 0.8% 0.2%;
  background-color: rgba(228, 228, 228, 0.5);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.user-identic .identic-block .id-user-links .id-link-image {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 0.3rem;
  user-select: none;
  text-decoration: none;
}
.user-identic .identic-block .id-user-links .id-link-image:hover {
  background-color: #ffffff;
}
