.create-post {
  width: 100%;
  height: 85vh;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}
.create-post .author-tools {
  margin-top: 3%;
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel {
  width: 16%;
  height: 100%;
  margin: 0 0.5% 0 0.5%;
  border-radius: 0.5rem;
  border: 2px solid #5C5D5D;
  background-color: inherit;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.create-post .author-tools .settings-panel .settings-label {
  width: 100%;
  height: 10%;
  font-size: 18px;
  font-weight: 600;
  color: #cecece;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid #5C5D5D;
}
.create-post .author-tools .settings-panel .access-block {
  width: 96%;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel .access-block .form-label {
  width: 80%;
  height: 40%;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  text-align: left;
}
.create-post .author-tools .settings-panel .access-block .select {
  background-color: #343637;
  color: #cecece;
  width: 90%;
  height: 50%;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  padding: 0 5% 0 5%;
  font-weight: 600;
  font-size: 15px;
}
.create-post .author-tools .settings-panel .access-block .select .option {
  font-size: 15px;
  font-weight: 700;
}
.create-post .author-tools .settings-panel .access-block .select .option .cat {
  background-color: #ffffff;
  width: 30%;
  color: #b4d9fc;
}
.create-post .author-tools .settings-panel .price-block {
  width: 96%;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel .price-block .form-label {
  width: 80%;
  height: 40%;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  text-align: left;
}
.create-post .author-tools .settings-panel .price-block .price-bar {
  background-color: #343637;
  color: #ffffff;
  width: 81%;
  height: 50%;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  padding: 0 5% 0 5%;
  font-weight: 600;
  font-size: 15px;
}
.create-post .author-tools .settings-panel .date-block {
  width: 96%;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel .date-block .form-label {
  width: 80%;
  height: 40%;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  text-align: left;
}
.create-post .author-tools .settings-panel .date-block .date-bar {
  background-color: #343637;
  color: #ffffff;
  width: 81%;
  height: 50%;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  padding: 0 5% 0 5%;
  font-weight: 600;
  font-size: 15px;
}
.create-post .author-tools .settings-panel .title-block {
  width: 96%;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel .title-block .form-label {
  width: 80%;
  height: 40%;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  text-align: left;
}
.create-post .author-tools .settings-panel .title-block .post-title {
  background-color: #343637;
  color: #ffffff;
  width: 80%;
  height: 50%;
  border: none;
  outline: none;
  border-radius: 0.5rem;
  padding: 0 5% 0 5%;
  font-weight: 600;
  font-size: 15px;
}
.create-post .author-tools .settings-panel .control-block {
  margin-top: 0.5rem;
  width: 96%;
  height: 15%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel .control-block .publish-now {
  margin: 0 0.2rem 0 0.2rem;
  width: 44%;
  height: 66%;
  background-color: #99CBF9;
  border: none;
  border-radius: 0.5rem;
  color: #343637;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .settings-panel .control-block .schedule-post {
  margin: 0 0.2rem 0 0.2rem;
  width: 42%;
  height: 60%;
  border: 2px solid #99CBF9;
  border-radius: 0.5rem;
  background-color: inherit;
  color: #cecece;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .editor-container {
  width: 38%;
  height: 100%;
  margin: 0 0.5% 0 0.5%;
  border: 2px solid #5C5D5D;
  background-color: inherit;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 0.5rem;
}
.create-post .author-tools .editor-container .image-carousel {
  width: 98%;
  height: 25%;
  background-color: inherit;
  padding: 1%;
  overflow-x: scroll;
  overflow-y: hidden;
  gap: 0.5rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.create-post .author-tools .editor-container .image-carousel .carousel-image {
  width: 30%;
  height: 90%;
  position: relative;
  background-color: #343637;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .editor-container .image-carousel .carousel-image .image-block {
  width: 100%;
  height: 90%;
  background-color: #99CBF9;
}
.create-post .author-tools .editor-container .image-carousel .carousel-image .img-img {
  width: 100%;
  height: 100%;
  min-width: 4rem;
  min-height: 4rem;
  aspect-ratio: 1 / 1;
  background-color: #51595C;
}
.create-post .author-tools .editor-container .image-carousel .carousel-image .image-controls {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}
.create-post .author-tools .editor-container .image-carousel .carousel-image .image-controls .img-btn {
  width: 28%;
  height: 100%;
  margin-left: 0.1rem;
  color: #b4d9fc;
  background-color: rbga(0, 0, 0, 0.1);
}
.create-post .author-tools .editor-container .text-editor {
  width: 100%;
  min-height: 200px;
  height: 65%;
  background-color: #51595C;
  border-color: transparent;
  color: #ffffff;
  overflow: scroll;
}
.create-post .author-tools .preview-container {
  border-radius: 0.5rem;
  overflow: scroll;
  width: 40%;
  height: 100%;
  margin: 0 0 0 1%;
  border: 2px solid #5C5D5D;
  background-color: #404649;
  display: flex;
  justify-content: center;
  align-items: center;
}
.create-post .author-tools .preview-container .preview-post-body {
  width: 96%;
  margin-top: 5vh;
  margin-bottom: 2rem;
  background-color: #51595C;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.create-post .author-tools .preview-container .preview-post-body .title {
  margin-top: 2%;
  width: 96%;
  height: 5%;
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #cecece;
}
.create-post .author-tools .preview-container .preview-post-body .preview-content-text {
  width: 96%;
  color: #ffffff;
  font-size: 16px;
  text-align: left;
}
.ql-toolbar {
  background-color: #f9f9f9;
  border-bottom: 1px solid #ddd;
}
.image-carousel {
  display: flex;
  overflow-x: auto;
  gap: 10px;
}
.carousel-image {
  position: relative;
}
.carousel-image img {
  width: 100px;
  height: 100px;
  object-fit: cover;
}
.carousel-image div {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  cursor: pointer;
  font-size: 12px;
}
.preview-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.image-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.image-preview img {
  width: 100px;
  height: 100px;
  object-fit: cover;
}
.text-preview {
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
}
.text-editor {
  min-height: 200px;
  min-width: 100%;
  background-color: #343637;
  font-size: 15px;
}
/*EDITOR*/
.ql-toolbar.ql-snow {
  background-color: #343637;
  width: 100%;
  border: none;
}
.ql-snow .ql-picker {
  color: #ffffff;
}
.ql-snow .ql-picker-label {
  color: #ffffff;
}
.ql-snow .ql-picker-options {
  background-color: #343637;
}
.ql-snow .ql-stroke {
  stroke: #ffffff;
}
.ql-snow .ql-fill {
  fill: #ffffff;
}
.ql-snow .ql-picker-item:hover,
.ql-snow .ql-picker-label:hover {
  color: #b4d9fc;
}
