.footer {
  width: 100%;
  height: 20%;
  background-color: #51595C;
  position: relative;
  bottom: -1rem;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer .content {
  padding-top: 1rem;
  width: 80%;
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer .content .upper-info {
  width: 100%;
  height: 330px;
  margin-bottom: 1rem;
  background-color: #343637;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.footer .content .upper-info .block {
  width: 32%;
  height: 100%;
  background-color: #d2d2d2;
}
.footer .content .lower-info {
  padding-bottom: 0.5rem;
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer .content .lower-info .credentials {
  font-size: 16px;
  color: #cecece;
  font-weight: 500;
}
