.actions {
  width: 75%;
  min-height: 18%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 0.6rem;
}
.actions .goals-block {
  width: 65%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.actions .challenge-block {
  width: 35%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.actions .act-partition-title {
  margin-top: 0.5rem;
  padding-left: 2%;
  height: 10%;
  width: 100%;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 1rem;
}
.act-goals-row {
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
}
.act-goal {
  transition: width 0.3s ease, transform 0.3s ease, z-index 0.3s, height 0.3s ease;
  cursor: pointer;
  background-color: #343637;
  color: #ffffff;
  overflow: hidden;
  position: relative;
  border-radius: 0.5rem;
  width: 5%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 3;
  box-shadow: 0 0 7px #424242;
}
.act-goal-unactive {
  height: 100%;
  color: #cecece;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.act-goal-unactive .act-img-unactive {
  width: 100%;
  height: 88%;
}
.act-goal-unactive .act-img-unactive .goal-img {
  width: 2.5rem;
  height: 2.5rem;
}
.act-goal-unactive .act-goal-num {
  margin-top: 0.3rem;
  max-height: 0.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.act-goal.inactive {
  width: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.act-goal.active {
  width: 55%;
  background-color: #51595C;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.act-goal.active .act-goal-info {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.act-goal.active .act-goal-info .act-goal-active-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.act-goal.active .act-goal-info .act-goal-active-content .act-goal-wrapper {
  width: 94%;
  height: 88%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.act-goal.active .act-goal-info .act-goal-active-content .act-goal-wrapper .act-goal-id {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.2rem;
}
.act-goal.active .act-goal-info .act-goal-active-content .act-goal-wrapper .act-goal-id .goal-img {
  width: 2.5rem;
  height: 2.5rem;
}
.act-goal.active .act-goal-info .act-goal-active-content .act-goal-wrapper .act-goal-active-title {
  width: 70%;
  height: 50%;
  margin-left: 0.35rem;
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.act-goal.active .act-goal-info .act-goal-active-content .act-goal-wrapper .toggle-button {
  width: 1.5rem;
  height: 1.5rem;
  background: none;
  border-radius: 0.5rem;
  cursor: pointer;
  margin-left: auto;
  margin-right: 2%;
}
.act-goal.expanded {
  width: 100%;
  z-index: 3;
  height: auto;
}
.act-goal-progressbar {
  background-color: #5e676b;
  width: 100%;
  height: 0.4rem;
  margin-top: 5px;
  overflow: hidden;
}
.act-goal-progressbar .act-progress {
  background-color: #99CBF9;
  height: 100%;
}
.expanded-content {
  margin-top: 10px;
  animation: expand 0.3s ease-in-out;
}
.donation-input {
  margin-top: 10px;
  padding: 5px;
  width: 80%;
}
.donate-button {
  margin-top: 5px;
  padding: 5px;
  background-color: #ff9900;
  border: none;
  color: #fff;
  cursor: pointer;
  width: 100%;
}
.progress-info {
  display: flex;
  justify-content: space-between;
  font-size: 0.8em;
  margin-top: 5px;
}
@keyframes expand {
  from {
    max-height: 0;
    opacity: 0;
  }
  to {
    max-height: 200px;
    opacity: 1;
  }
}
/* BIDS */
.act-challenge-block {
  width: 100%;
  height: 100%;
  background-color: #51595C;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.act-challenge-block .bid-info {
  width: 73%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.act-challenge-block .bid-info .bid-title {
  width: 100%;
  height: 45%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  color: #ffffff;
  font-weight: 500;
  font-size: 15px;
}
.act-challenge-block .bid-info .bid-title .bid-author {
  color: #99CBF9;
}
.act-challenge-block .bid-info .bid-descr {
  width: 100%;
  height: 54%;
  font-size: 14px;
  font-weight: 300;
  color: #cecece;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: left;
}
.act-challenge-block .bid-button {
  width: 14%;
  margin-left: 3%;
  height: 65%;
  aspect-ratio: 1/1;
  border-radius: 0.5rem;
  background-color: #99CBF9;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
}
.act-challenge-block .bid-button .bid-icon {
  width: 50%;
  height: 50%;
}
.act-challenge-block .bid-button .bid-btn-text {
  width: 80%;
  height: 20%;
  color: #343637;
  font-weight: 500;
}
