.reg-block {
  position: absolute;
  background-color: #404649;
  width: 100%;
  height: 100%;
  z-index: 5;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  overflow: hidden;
}
.reg-block .reg-content {
  width: 60%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.reg-block .reg-content .reg-label {
  justify-self: self-start;
  align-self: center;
  width: 80%;
  height: 50%;
  margin-top: 5%;
  font-size: 90px;
  font-weight: bold;
  text-align: left;
  color: #cecece;
  user-select: none;
}
.reg-block .reg-content .figure {
  position: relative;
  width: 95%;
  height: 40%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.reg-block .reg-content .figure .rect1 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 30rem;
  height: 30rem;
  border-radius: 1rem;
  background-color: #cecece;
  z-index: 7;
  transform: rotate(45deg) translate(9rem, 17rem);
}
.reg-block .reg-content .figure .rect2 {
  position: absolute;
  width: 15rem;
  height: 15rem;
  border-radius: 1rem;
  background-color: #99CBF9;
  z-index: 6;
  transform: rotate(45deg) translate(3rem, 7rem);
}
.reg-block .reg-content .figure .rect3 {
  position: absolute;
  width: 20rem;
  height: 20rem;
  border-radius: 1rem;
  background-color: #cecece;
  z-index: 7;
  transform: rotate(45deg) translate(17rem, 2rem);
}
.reg-block .reg-form {
  width: 30%;
  height: 82%;
  margin-right: 5%;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.reg-block .reg-form .form-title {
  width: 100%;
  height: 7%;
  margin-bottom: 3%;
  font-size: 28px;
  font-weight: 500;
  color: #cecece;
  text-align: left;
  user-select: none;
}
.reg-block .reg-form .reg-form-panel {
  width: 10rem;
  height: 100%;
  max-width: 500px;
  max-height: 600px;
  min-width: 450px;
  min-height: 450px;
  border: 2px solid #5C5D5D;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .reg-fields {
  margin-top: 5%;
  width: 95%;
  height: 55%;
  display: flex;
  flex-direction: column;
}
.reg-block .reg-form .reg-form-panel .reg-fields .reg-form-block {
  margin-top: 1%;
  width: 100%;
  height: 45%;
  display: flex;
  flex-direction: column;
}
.reg-block .reg-form .reg-form-panel .reg-fields .reg-form-block .form-label {
  width: 100%;
  height: 25%;
  color: #cecece;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
}
.reg-block .reg-form .reg-form-panel .reg-fields .reg-form-block .form-input {
  margin-top: 2%;
  width: 100%;
  height: 74%;
  border: 2px solid #5C5D5D;
  border-radius: 0.4rem;
  color: #5C5D5D;
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .form-braker {
  width: 95%;
  height: 14%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #6e6e6e;
  font-size: 22px;
  font-weight: 600;
}
.reg-block .reg-form .reg-form-panel .reg-lower {
  width: 95%;
  height: 24%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway {
  width: 100%;
  height: 45%;
  border: 2px solid #5C5D5D;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner {
  width: 97%;
  height: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .link {
  width: 32%;
  height: 90%;
  background-color: #99CBF9;
  border-radius: 0.3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .link-glg {
  background-color: #F7F7F7;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .link-meta {
  background-color: #4267B2;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .link-x {
  background-color: #000;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .logo {
  width: 2.2rem;
  height: 2.2rem;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .logo-meta {
  width: 2.2rem;
  height: 1.5rem;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .name {
  width: 60%;
  height: 72%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .name-glg {
  color: #6e6e6e;
  font-weight: 600;
}
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .name-meta,
.reg-block .reg-form .reg-form-panel .reg-lower .reg-otherway .otherway-contailner .name-x {
  color: #cecece;
}
.reg-block .reg-form .reg-form-panel .reg-lower .confirmation {
  width: 95%;
  height: 35%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.reg-block .reg-form .reg-form-panel .reg-lower .confirmation .login-button {
  width: 50%;
  height: 90%;
  border-radius: 0.5rem;
  background-color: #99CBF9;
  color: #404649;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: 600;
  margin-right: 2%;
  user-select: none;
  cursor: pointer;
  transition: background-color 0.2s ease-in;
}
.reg-block .reg-form .reg-form-panel .reg-lower .confirmation .login-button:hover {
  background-color: #b4d9fc;
}
.reg-block .reg-form .reg-form-panel .reg-lower .confirmation .recover-button {
  width: 40%;
  height: 60%;
  margin-left: 3%;
  font-weight: 500;
  font-size: 14px;
  color: #6e6e6e;
  text-align: left;
}
.reg-block .reg-form .reg-form-panel .reg-lower .confirmation .recover-button .recover-link {
  font-weight: bold;
  color: #99CBF9;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.2s ease-in;
}
.reg-block .reg-form .reg-form-panel .reg-lower .confirmation .recover-button .recover-link:hover {
  color: #b4d9fc;
}
