.recommendation-block {
  padding: 0;
  margin-top: 2%;
  width: 100%;
  height: 20rem;
  border-radius: 0.7rem;
  background-color: #51595C;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.recommendation-block .rec-title {
  width: 90%;
  height: 10%;
  margin-bottom: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
}
.recommendation-block .rec-authors-list {
  width: 90%;
  height: 74%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.recommendation-block .rec-authors-list .rec-author {
  width: 25%;
  height: 75%;
  border-radius: 0.5rem;
  margin: 0 1% 0 1%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #5e676b;
}
.recommendation-block .rec-authors-list .rec-author .rec-image-block {
  width: 100%;
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background: #afbbbb;
  background: -moz-linear-gradient(0deg, #afbbbb 0%, #494f50 100%);
  background: -webkit-linear-gradient(0deg, #afbbbb 0%, #494f50 100%);
  background: linear-gradient(0deg, #afbbbb 0%, #494f50 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#afbbbb", endColorstr="#494f50", GradientType=1);
  border-radius: 0.5rem 0.5rem 0 0;
  position: relative;
}
.recommendation-block .rec-authors-list .rec-author .rec-image-block .rec-splash {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem 0.5rem 0 0;
}
.recommendation-block .rec-authors-list .rec-author .rec-image-block .rec-author-image {
  position: absolute;
  width: 5rem;
  height: 5rem;
  background-color: #b4d9fc;
  border-radius: 0.5rem;
  border: 3px solid #99CBF9;
}
.recommendation-block .rec-authors-list .rec-author .rec-author-name {
  width: 80%;
  height: 15%;
  color: #ffffff;
  font-size: 15px;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
.recommendation-block .rec-authors-list .rec-author .rec-author-headline {
  width: 80%;
  height: 20%;
  color: #cecece;
  font-size: 14px;
  font-weight: 300;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.recommendation-block .rec-buttons {
  width: 90%;
  height: 12%;
  margin-top: 1%;
  margin-bottom: 2%;
  color: #99CBF9;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.recommendation-block .rec-buttons .carousel-btn {
  width: 7%;
  height: 100%;
  color: #99CBF9;
  border: 2px solid #99CBF9;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}
.recommendation-block .rec-buttons .carousel-btn:hover {
  background-color: #99CBF9;
  color: #404649;
  border-color: transparent;
  font-weight: 700;
  font-size: 20px;
}
.recommendation-block .rec-buttons .rec-show-more {
  width: 20%;
  height: 100%;
  border-radius: 0.5rem;
  border: 2px solid #99CBF9;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10% 0 10%;
}
.recommendation-block .rec-buttons .rec-show-more:hover {
  background-color: #99CBF9;
  color: #404649;
  border-color: transparent;
}
