.donation-goal {
  width: 100%;
  height: auto;
  min-height: 20rem;
  margin-top: 3%;
  border-radius: 0.7rem;
  background-color: #51595C;
  box-shadow: #424242 0 0 10px;
}
.donation-goal .post-attribution {
  width: 100%;
  height: 5%;
  max-height: 5%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.donation-goal .post-attribution .author-credential {
  width: 70%;
  height: 90%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-left: 2%;
  padding-top: 2%;
  padding-bottom: 1.5%;
}
.donation-goal .post-attribution .author-credential .author-image {
  margin-right: 2%;
  max-width: 1.5rem;
  max-height: 1.5rem;
  border-radius: 0.3rem;
  box-shadow: #424242 0 0 5px;
  background-color: #b4d9fc;
}
.donation-goal .post-attribution .author-credential .author-name {
  width: 70%;
  height: 80%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-size: 16px;
  font-weight: 500;
  color: #cecece;
}
.donation-goal .post-attribution .post-date {
  width: 10%;
  height: 90%;
  margin-right: 2%;
  font-size: 14px;
  font-weight: 200;
  color: #cecece;
  border-radius: 0.3rem;
}
.donation-goal .post-image-block {
  background: #343637;
  background: linear-gradient(90deg, #343637 0%, #51595c 25%, #51595c 50%, #51595c 75%, #343637 100%);
}
.donation-goal .post-image-block .image-wrapper {
  width: 100%;
  position: relative;
  padding-top: 56.25%;
  overflow: hidden;
}
.donation-goal .post-image-block .post-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
.donation-goal .post-info-block {
  width: 100%;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.donation-goal .post-info-block .main-info-block {
  width: 95%;
  display: flex;
  margin-top: 3%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.donation-goal .post-info-block .main-info-block .post-title {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: flex-start;
  font-size: 20px;
  font-weight: 600;
  color: #cecece;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
}
.donation-goal .post-info-block .main-info-block .post-description {
  width: 100%;
  height: auto;
  margin-top: 3%;
  display: flex;
  justify-content: flex-start;
  text-align: left;
  font-size: 14px;
  font-weight: 200;
  color: #cecece;
  letter-spacing: 1px;
}
.donation-goal .post-info-block .main-info-block .open-post-button {
  width: 10%;
  height: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  color: #99CBF9;
  align-self: flex-start;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  transition: font-size 0.15s, color 0.15s ease-in;
}
.donation-goal .post-info-block .main-info-block .open-post-button:hover {
  color: #b4d9fc;
}
.donation-goal .post-info-block .donation-progress {
  width: 95%;
  display: flex;
  margin-top: 3%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.donation-goal .post-info-block .donation-progress .progressbar-block {
  margin: 2%;
  width: 75%;
  height: 0.5rem;
  background-color: #6b8094;
  border-radius: 0.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.donation-goal .post-info-block .donation-progress .progressbar-block .progress-bar {
  height: 0.5rem;
  background-color: #99CBF9;
  border-radius: 0.2rem;
}
.donation-goal .post-info-block .donation-progress .progress-text {
  font-size: 16px;
  font-weight: 500;
  color: #cecece;
}
.donation-goal .post-info-block .button-block {
  margin-top: 4%;
  margin-bottom: 1rem;
  width: 95%;
  height: 20%;
}
.donation-goal .post-info-block .button-block .donate-button {
  padding-top: 1%;
  padding-bottom: 1%;
  width: 25%;
  height: 80%;
  background-color: inherit;
  border: 2px solid #99CBF9;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
}
.donation-goal .post-info-block .button-block .donate-button .btn-text {
  width: 70%;
  height: 80%;
  color: #99CBF9;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.2s, font-weight 0.1s ease-in;
}
.donation-goal .post-info-block .button-block .donate-button .btn-currency {
  width: 1rem;
  height: 1rem;
  background-color: inherit;
  transition: transform 0.2s ease-in-out;
}
.donation-goal .post-info-block .button-block .donate-button:hover {
  border-color: #b4d9fc;
}
.donation-goal .post-info-block .button-block .donate-button:hover .btn-text {
  color: #b4d9fc;
  font-weight: 600;
}
.donation-goal .post-info-block .button-block .donate-button:hover .btn-currency {
  transform: translateX(-20%);
}
