.describer {
  margin-top: 0.3rem;
  width: 75%;
  height: 10%;
  border-radius: 0.5rem;
  border: 2px solid #5C5D5D;
  background-color: inherit;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #cecece;
  font-size: 16px;
}
.describer .dsc-short-desc {
  max-width: 33%;
  min-width: 33%;
  margin-left: 2%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-weight: 600;
  overflow: hidden;
  white-space: nowrap;
}
.describer .dsc-subs {
  min-width: 30%;
  height: 80%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.describer .dsc-posts {
  max-width: 20%;
  min-width: 20%;
  height: 80%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.describer .dsc-subscribe-button {
  max-width: 13%;
  min-width: 13%;
  height: 80%;
  background-color: #99CBF9;
  color: #404649;
  font-size: 18px;
  font-weight: 600;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  user-select: none;
}
.describer .dsc-subscribe-button:hover {
  background-color: #b4d9fc;
}
.describer .dsc-subscription-level {
  max-width: 13%;
  min-width: 13%;
  height: 80%;
  background-color: #51595C;
  color: #cecece;
  font-size: 18px;
  font-weight: 600;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  user-select: none;
}
.describer .dsc-subscription-level .dsc-sublevel-title {
  width: 40%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}
.describer .dsc-subscription-level .dsc-sublevel-value {
  width: 15%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 18px;
  transition: 0.2s text-shadow ease-in;
}
.describer .dsc-subscription-level .dsc-sublevel-upgrade {
  width: 1.4rem;
  height: 1.4rem;
  margin-left: 1%;
  margin-right: 1%;
  transition: 0.3s transform ease-in-out;
}
.describer .dsc-subscription-level:hover .dsc-sublevel-upgrade {
  transform: translateY(-0.3rem);
}
.describer .dsc-subscription-level:hover .dsc-sublevel-value {
  text-shadow: #ffffff 0 0 10px;
}
.describer .dsc-value {
  color: #ffffff;
  font-weight: 600;
  margin-left: 1%;
  margin-right: 2%;
}
