.plans-section {
  width: 75%;
  min-height: 80%;
  margin-top: 0.5rem;
  border-radius: 0.5rem;
  border: 2px solid #5C5D5D;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.plans-section .plans-title {
  width: 74%;
  height: 10%;
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
}
.plans-section .plans-block {
  width: 74%;
  height: 80%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.plans-section .plans-block .plan {
  height: 100%;
  width: 32%;
  background-color: #51595C;
  margin-left: 0.5%;
  margin-right: 0.5%;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.plans-section .plans-block .plan .plan-image {
  width: 100%;
  height: 33%;
  background-color: #1a1a1a;
  border-radius: 0.5rem 0.5rem 0 0;
}
.plans-section .plans-block .plan .plan-level {
  width: 100%;
  height: 12%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  color: #cecece;
  text-transform: uppercase;
}
.plans-section .plans-block .plan .plan-level .plan-level-value {
  color: #99CBF9;
  margin: 2%;
}
.plans-section .plans-block .plan .plan-content {
  width: 95%;
  height: 50%;
  background-color: inherit;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  border-radius: 0.5rem;
  border: 1px solid #8E8E8E;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.plans-section .plans-block .plan .plan-content .plan-advantages {
  width: 96%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.plans-section .plans-block .plan .plan-content .plan-advantages .advantage {
  width: 90%;
  height: 15%;
  margin-top: 0.2rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.plans-section .plans-block .plan .plan-content .plan-advantages .advantage .adv-title {
  width: 80%;
  height: 100%;
  text-align: left;
  font-weight: 500;
  color: #cecece;
  font-size: 15px;
}
.plans-section .plans-block .plan .plan-content .plan-advantages .advantage .adv-flag {
  width: 20%;
  height: 100%;
  color: #cecece;
  font-weight: 600;
}
.plans-section .plans-block .plan .plan-content .plan-confirm {
  width: 96%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 0.5rem;
}
.plans-section .plans-block .plan .plan-content .plan-confirm .plan-price {
  width: 35%;
  height: 90%;
  color: #cecece;
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}
.plans-section .plans-block .plan .plan-content .plan-confirm .plan-button {
  width: 50%;
  height: 80%;
  background-color: #99CBF9;
  color: #343637;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  border-radius: 0.5rem;
}
