.notification {
  width: 100%;
  height: 85vh;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}
.notification .notifications-filter {
  width: 20%;
  height: 88%;
  border: 2px solid #5C5D5D;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-filter .nots-stats {
  margin-top: 3%;
  border-radius: 0.5rem;
  width: 70%;
  height: 35%;
  background-color: #51595C;
}
.notification .notifications-filter .nots-filter {
  margin-top: 0.5rem;
  width: 100%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content {
  width: 70%;
  height: 90%;
  border-radius: 0.5rem;
  box-shadow: #394043 0 0 10px;
  background-color: #51595C;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-head {
  width: 90%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-head .filter-title {
  width: 60%;
  height: 100%;
  color: #cecece;
  font-size: 20px;
  font-weight: 500;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-head .filter-reset {
  color: #6e6e6e;
  width: 25%;
  height: 60%;
  font-weight: 500;
  font-size: 18px;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-head .filter-reset:hover {
  background-color: #677579;
  color: #cecece;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-body {
  width: 90%;
  height: 75%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-body .filter-user {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-body .filter-user .filter-option {
  margin-bottom: 1%;
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-body .filter-user .filter-option .filter-checkbox {
  width: 1rem;
  height: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  user-select: none;
  accent-color: #99CBF9;
  border: 1px solid #6e6e6e;
  background-color: #51595C;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-body .filter-user .filter-option .filter-value {
  width: 50%;
  margin-left: 2%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  font-weight: 300;
  color: #cecece;
}
.notification .notifications-filter .nots-filter .nots-filter-content .filter-body .filter-user .filter-option:hover .filter-value {
  font-weight: 500;
}
.notification .notifications-main {
  width: 68%;
  height: 88%;
  margin-left: 0.5rem;
  border: 2px solid #5C5D5D;
  border-radius: 0.5rem;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}
.notification .notifications-main .nots-content {
  width: 98%;
  height: 98%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-main .nots-content .nots-title {
  margin-top: 3%;
  width: 98%;
  height: 7%;
  font-size: 20px;
  font-weight: 600;
  color: #cecece;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
.notification .notifications-main .nots-content .notifications-block {
  width: 100%;
  height: auto;
  overflow-x: hidden;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block {
  width: 100%;
  height: auto;
  min-height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .nots-divider {
  width: 100%;
  height: 10vh;
  font-size: 18px;
  font-weight: 600;
  color: #cecece;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding-bottom: 0.5rem;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item {
  width: 100%;
  height: 7vh;
  background-color: #51595C;
  margin-bottom: 0.4rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
  transition: height 0.2s ease-in;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item .img-category {
  width: 2.5rem;
  height: 2.5rem;
  background-color: #99CBF9;
  border-radius: 3rem;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item .notification-data {
  width: 69%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 1%;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item .notification-data .notification-title {
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 2%;
  color: #cecece;
  font-size: 18px;
  font-weight: 400;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item .notification-data .notification-message {
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 2%;
  color: #cecece;
  font-size: 14px;
  font-weight: 200;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item .notification-date {
  width: 22%;
  height: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 2%;
  color: #cecece;
  font-size: 16px;
  font-weight: 300;
}
.notification .notifications-main .nots-content .notifications-block .timesort-block .notification-item:hover {
  height: 10vh;
}
