.header {
  background-color: #343637;
  position: fixed;
  top: 0;
  width: 100%;
  height: 7%;
  max-height: 100px;
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d2d2d2;
  z-index: 8;
}
.header .content {
  width: 80%;
  height: 80%;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-self: center;
}
.header .content .logo-link {
  width: 25%;
  height: 3rem;
  padding-right: 45%;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  color: #cecece;
}
.header .content .logo-link .logo-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 5rem;
}
.header .content .logo-link .logo-title {
  margin-left: 5%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 60%;
  height: 90%;
  font-size: 20px;
  font-weight: 700;
  line-height: -5px;
  text-decoration: none;
  user-select: none;
  white-space: nowrap;
}
.header .content .logo-link .logo-title .dot {
  font-size: 40px;
  font-weight: 900;
  color: #99CBF9;
}
.header .content .user-block {
  width: 25%;
  height: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header .content .user-block .user-icon {
  width: 2.8rem;
  height: 2.8rem;
  margin-right: 4%;
  border: 3px solid #5AE4A8;
  border-radius: 5rem;
  background-color: rgba(228, 228, 228, 0.5);
}
.header .content .user-block .user-icon.author {
  border-color: #99CBF9;
}
.header .content .user-block .user-identical {
  width: 75%;
  height: 90%;
}
.header .content .user-block .user-identical .username {
  width: 100%;
  height: 40%;
  background-color: inherit;
  color: #cecece;
  font-weight: bold;
  font-size: 16px;
  text-align: left;
}
.header .content .user-block .user-identical .user-role {
  background-color: #5AE4A8;
  width: 24%;
  height: 45%;
  margin-top: 5%;
  border-radius: 0.3rem;
  color: #343637;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
}
.header .content .user-block .user-identical .user-role.author {
  width: 33%;
  background-color: #99CBF9;
}
.header .content .settings-link {
  width: 10%;
  height: 3rem;
  float: right;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.header .content .settings-link .settings-icon {
  width: 1.5rem;
  height: 1.5rem;
  transition: transform 0.4s ease;
}
.header .content .settings-link .settings-icon:hover {
  transform: rotate(180deg);
}
